import { Button, FormLabel } from "react-bootstrap";
import FormModal from "../../../components/Modal/FormModal";
import { useEffect, useMemo, useState } from "react";
import { createAppointment } from "../../../app/features/appointment/appointment.slice";
import { useDispatch } from "react-redux";
import moment from "moment";
import { getAllSlots } from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import {
  CardCvcElement,
  CardExpiryElement,
  CardNumberElement,
  Elements,
  useElements,
  useStripe,
} from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import { Notification } from "../../../components/GenericActions";
import { ToastContainer } from "react-toastify";
import { ClipLoader } from "react-spinners";

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_KEY);

export default function PaymentCheckout(props) {
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { handleClose, show, singleSlotData } = props;
  const [isLoading, setIsLoading] = useState(false);
  const dispatch = useDispatch();

  const moveToNext = () => {
    handleClose();
    setIsLoading(false);
    const finalData = {
      date: moment(singleSlotData?.scheduleDate).format("YYYY-MM-DD"),
      month: null,
    };
    dispatch(getAllSlots(finalData));
  };

  function newCoachHire(tokenId) {
    let finalData = {
      ...props.formData,
      payment: {
        patientId: user?.userId,
        token: tokenId,
        scheduleDetailId: singleSlotData?.scheduleDetailId,
      },
    };
    dispatch(createAppointment({ finalData, moveToNext }));
  }
  return (
    <>
      <ToastContainer />
      <FormModal
        handleClose={handleClose}
        show={show}
        title="Checkout"
        className="modal-stripe"
        backdrop="static"
      >
        <Elements stripe={stripePromise}>
          <HireCoachForm
            isLoading={isLoading}
            setIsLoading={setIsLoading}
            newCoachHire={newCoachHire}
            singleSlotData={singleSlotData}
          />
        </Elements>
      </FormModal>
    </>
  );
}

const useOptions = () => {
  const fontSize = useResponsiveFontSize();
  const options = useMemo(
    () => ({
      style: {
        base: {
          fontSize,
          color: "#424770",
          letterSpacing: "0.025em",
          fontFamily: "Source Code Pro, monospace",
          "::placeholder": {
            color: "#aab7c4",
          },
        },
        invalid: {
          color: "#9e2146",
        },
      },
    }),
    [fontSize]
  );

  return options;
};

const HireCoachForm = ({
  isLoading,
  setIsLoading,
  newCoachHire,
  singleSlotData,
}) => {
  const stripe = useStripe();
  const elements = useElements();
  const options = useOptions();

  const formattedFee = singleSlotData?.fee?.toFixed(2);

  const handleSubmit = async (event) => {
    setIsLoading(true);
    event.preventDefault();

    if (!stripe || !elements) {
      return;
    }

    try {
      const card = elements.getElement(CardNumberElement);
      const result = await stripe.createToken(card);

      if (result.error) {
        throw new Error(result?.error?.message);
      }

      const tokenId = result?.token?.id;
      if (tokenId) {
        newCoachHire(tokenId);
      }
    } catch (error) {
      Notification(error?.message, "error");
      setIsLoading(false);
    }
  };

  return (
    <div className="py-2 stripe">
      <div>
        <FormLabel>Appointment Fee</FormLabel>
        <input
          type="text"
          value={`â‚¬${formattedFee ? formattedFee : "N/A"}`}
          disabled
          className="mt-0 w-100"
        />
      </div>
      <form onSubmit={handleSubmit}>
        <div>
          Card number
          <CardNumberElement options={options} />
        </div>
        <div>
          Expiration date
          <CardExpiryElement options={options} />
        </div>
        <div>
          CVC
          <CardCvcElement options={options} />
        </div>
        <div className="d-flex justify-content-center mt-3">
          <Button
            variant="primary"
            className="w-100 py-2 primary_bg"
            radius="0px"
            type="submit"
            disabled={isLoading}
          >
            {isLoading ? (
              <ClipLoader color="white" size={25} className="d-flex m-auto" />
            ) : (
              "Confirm"
            )}
          </Button>
        </div>
      </form>
    </div>
  );
};

function useResponsiveFontSize() {
  const getFontSize = () => (window.innerWidth < 450 ? "16px" : "18px");
  const [fontSize, setFontSize] = useState(getFontSize);

  useEffect(() => {
    const onResize = () => {
      setFontSize(getFontSize());
    };

    window.addEventListener("resize", onResize);

    return () => {
      window.removeEventListener("resize", onResize);
    };
  });

  return fontSize;
}
