import { Col, Row, Form, FormLabel, Button } from "react-bootstrap";
import FormModal from "../../../components/Modal/FormModal";
import IMAGES from "../../../assets/images";
import { useForm } from "react-hook-form";
import moment from "moment";
import { useDispatch } from "react-redux";
import { GrDocumentCloud } from "react-icons/gr";
import convertBase64, {
  convertTimeIntoUTC,
  convertTo24Hour,
} from "../../../components/GenericActions";
import { AiOutlineClose } from "react-icons/ai";
import { getAllSlots } from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import { useState } from "react";
import AWSImageUpload from "../../../app/features/bucketImageUpload/bucketImageUpload.service";
import PaymentCheckout from "./PaymentCheckout";

export default function AddNewAppointment({ children, ...rest }) {
  const { show, handleClose, singleSlotData, appointmentId } = rest;
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const [paymentCheckoutModel, setPaymentCheckoutModel] = useState(false);
  const [selectedFiles, setSelectedFiles] = useState();
  const [imageUrl, setImageUrl] = useState();
  const [loading, setLoading] = useState();
  const [consultationType, setConsultationType] = useState({
    isVideo: true,
    isAudio: false,
  });
  const dispatch = useDispatch();
  // const handleFileChange = (files) => {
  //   setSelectedFiles([...files]);
  // };

  const { register, handleSubmit, reset } = useForm();

  const handleConsultationTypeChange = (e) => {
    const selectedType = e.target.value;
    setConsultationType({
      isVideo: selectedType === "Video",
      isAudio: selectedType === "Audio",
    });
  };
  const handleRemoveFile = () => {
    setSelectedFiles(null);
  };
  const handleCloseModal = () => {
    setPaymentCheckoutModel(false)
    reset({
      reasonForAppoinment: "",
    });
    setSelectedFiles(null);
    setImageUrl(null);
    setConsultationType({
      isVideo: true,
      isAudio: false,
    });
    handleClose();
  };

  const moveToNext = () => {
    handleClose();
    const finalData = {
      date: moment(singleSlotData?.scheduleDate).format("YYYY-MM-DD"),
      month: null,
    };
    dispatch(getAllSlots(finalData));
    reset(
      {
        reasonForAppoinment: "",
      },
      {
        password: "",
      },
      {
        startTime: "",
      },
      {
        name: "",
      },
      {
        email: "",
      }
    );
  };

  const [formData, setFormData] = useState({});

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleChangeUpload = async (e) => {
    let file = e.target.files[0];
    setSelectedFiles(file);
    if (file) {
      const lastIndexOfDot = file.name.lastIndexOf(".");
      const name = file.name.slice(0, lastIndexOfDot);
      const ext = file.name.slice(lastIndexOfDot + 1, file.name.length);
      // convertBase64(file, (url) => {
      if (ext.toLowerCase() === "pdf") {
        const reader = new FileReader();
        reader.onload = async (event) => {
          const base64String = event.target.result;

          setLoading(true);

          const fileData = {
            name: name,
            base64: base64String.split(",")[1],
            fileExtension: `${ext}`,
          };
          AWSImageUpload(fileData).then((response) => {
            if (response) {
              setImageUrl(response);
              setLoading(false);
            } else {
              // Handle the error
            }
          });
        };
        reader.onerror = (error) => {
          // Handle the error
        };

        reader.readAsDataURL(file);
      } else {
        // Handle non-PDF file types here
        // For other file types, you can use your existing code to convert to Base64
        convertBase64(file).then((url) => {
          setLoading(true);

          const fileData = {
            name: name,
            base64: url,
            fileExtension: `${ext}`,
          };

          AWSImageUpload(fileData).then((response) => {
            if (response) {
              setImageUrl(response);
              setLoading(false);
            } else {
              // Handle the error
            }
          });
        });
      }
    }
  };
  function onSubmit() {
    const finalData = {
      scheduleId: singleSlotData?.scheduleId,
      appointmentDate: moment(singleSlotData?.scheduleDate).format(
        "YYYY-MM-DD"
      ),
      patientId: user?.userId,
      appointmentStartTime: convertTimeIntoUTC(
        convertTo24Hour(singleSlotData?.startTime),
        moment(singleSlotData?.scheduleDate).format("YYYY-MM-DD")
      )?.split("Z")[0],
      appointmentEndTime: convertTimeIntoUTC(
        convertTo24Hour(singleSlotData?.endTime),
        moment(singleSlotData?.scheduleDate).format("YYYY-MM-DD")
      )?.split("Z")[0],
      reasonForAppoinment: formData?.reasonForAppoinment,
      isAudio: consultationType.isAudio,
      isVideo: consultationType.isVideo,
      image: imageUrl?.keyName ? imageUrl?.keyName : null,
    };
    setFormData(finalData);
    handleClose();
    setPaymentCheckoutModel(true);
  }
  return (
    <div className="AddSlot_modal">
      <FormModal
        className="modal-lg"
        title={appointmentId ? "Reschedule Appointment" : "Book Appointment"}
        handleClose={handleCloseModal}
        show={show}
      >
        <div
          className="px-2 pb-3"
          style={{
            height: "640px",
            "overflow-y": "scroll",
            "overflow-x": "hidden",
          }}
        >
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row>
              <Col xl={12}>
                <Form.Group controlId="formDate" className="mb-3">
                  <FormLabel className="fw-semibold fs-6">
                    Reason for Appointment
                  </FormLabel>
                  <Form.Control
                    as="textarea"
                    name="reasonForAppoinment"
                    placeholder="Type here"
                    style={{ height: "100px" }}
                    {...register("reasonForAppoinment")}
                    value={formData.reasonForAppoinment}
                    onChange={handleInputChange}
                  />
                </Form.Group>
              </Col>

              <Col md={6}>
                <Form.Group controlId="" className="mb-3">
                  <FormLabel className="fw-semibold fs-6">Date</FormLabel>
                  <Form.Control
                    type="date"
                    placeholder="HH:MM"
                    className="custom-date"
                    defaultValue={moment(singleSlotData?.scheduleDate).format(
                      "YYYY-MM-DD"
                    )}
                    disabled
                    name="appointmentDate"
                    {...register("appointmentDate")}
                  />
                </Form.Group>

                <Form.Group controlId="" className="mb-3">
                  <FormLabel className="fw-semibold fs-6">Name</FormLabel>
                  <Form.Control
                    type="text"
                    placeholder="John Smith"
                    className="custom-date"
                    name="name"
                    defaultValue={user?.name}
                    disabled
                    {...register("name")}
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group controlId="" className="mb-3">
                  <FormLabel className="fw-semibold fs-6">Time</FormLabel>
                  <Form.Control
                    type="text"
                    placeholder="HH:MM"
                    className="custom-date"
                    name="startTime"
                    defaultValue={`${singleSlotData?.startTime} - ${singleSlotData?.endTime}`}
                    disabled
                  />
                </Form.Group>
                <Form.Group controlId="" className="mb-3">
                  <FormLabel className="fw-semibold fs-6">Email</FormLabel>
                  <Form.Control
                    type="email"
                    placeholder="johnsmith@gmail.com"
                    className="custom-date"
                    name="email"
                    defaultValue={user?.email}
                    disabled
                    {...register("email")}
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Label className="fw-bold">Consultation type</Form.Label>
                <Form.Select
                  aria-label="Select Consultation Type"
                  onChange={handleConsultationTypeChange}
                  value={consultationType.isVideo ? "Video" : "Audio"}
                >
                  <option value="Video">Video</option>
                  <option value="Audio">Audio</option>
                </Form.Select>
              </Col>
              <Col xl={12} className="upload_pic mt-3">
                <FormLabel className="fw-semibold fs-6">Attachments</FormLabel>
                <div className="mb-2 border py-3 rounded">
                  <label
                    htmlFor="patient-pic"
                    className="text-center w-100 pt-1"
                  >
                    <img
                      className="mb-2 upload-icon"
                      width=""
                      src={IMAGES.UPLOAD_ICON}
                      alt="location"
                    />
                    <p className="upload-text mb-1">
                      Upload a file{" "}
                      <span className="text-black fs-6"> or drag and drop</span>
                    </p>
                    <p className="upload-text_small mb-0">
                      PNG, JPG, PDF upto 5MB
                    </p>
                  </label>
                  <input
                    size="small"
                    type="file"
                    id="patient-pic"
                    name="patient-pic"
                    accept="image/png, image/jpeg, application/pdf"
                    multiple
                    onChange={(e) => {
                      handleChangeUpload(e);
                    }}
                    onDrop={(e) => {
                      e.preventDefault();
                      // const droppedFiles = e.dataTransfer.files;
                      handleChangeUpload(e);
                    }}
                    onDragOver={(e) => e.preventDefault()}
                  />
                </div>
                <div className="my-3">
                  {/* {selectedFiles.map((file) => (
                    <div
                      className="d-flex align-items-center pb-1"
                      key={file.name}
                    >
                      <GrDocumentCloud size={30} style={{ color: "#745DED" }} />
                      <h6 className="file-name mb-0 ms-2">{file.name}</h6>
                    </div>
                  ))} */}
                  {selectedFiles && (
                    <div className="d-flex align-items-center pb-1">
                      <GrDocumentCloud size={30} style={{ color: "#745DED" }} />
                      <h6 className="file-name mb-0 ms-2">
                        {selectedFiles?.name}
                      </h6>
                      <span className="mx-3">
                        <AiOutlineClose size={18} onClick={handleRemoveFile} />
                      </span>
                    </div>
                  )}
                </div>
                <div className="d-flex align-items-start">
                  <img
                    className="me-2"
                    width=""
                    src={IMAGES.INFO_ICON}
                    alt="location"
                  />
                  <p className="upload-text_small mb-0 fs-6">
                    {" "}
                    <span className="text_darkGray fw-semibold">
                      {" "}
                      You can upload the following:
                    </span>{" "}
                    Evidence for repeat prescription, reports, documents, clear
                    photo of rash or lesion (Important Note: Please do not
                    upload blood test results or images of intimate areas of
                    your body)
                  </p>
                </div>
              </Col>
              {/* 
            <h4 className='fw-semibold fs-5 mt-4'>Soft Reminder</h4>
            <Row>
              <Col md={6}>
                <Form.Group controlId="" className='mb-3'>
                  <FormLabel className='fw-semibold fs-6'>Date</FormLabel>
                  <Form.Control type="date" placeholder="HH:MM" className="custom-date" />
                </Form.Group>
              </Col>
              <Col md={6}>
              <Form.Group controlId="" className='mb-3'>
                  <FormLabel className='fw-semibold fs-6'>Time</FormLabel>
                  <Form.Control type="time" placeholder="" className="custom-date" />
                </Form.Group>
              </Col>
            </Row> */}

              <div className="d-grid mt-4">
                {appointmentId ? (
                  <Button
                    className="block primary_bg"
                    variant="primary"
                    size="lg"
                    type="submit"
                  >
                    Reschedule Appointment
                  </Button>
                ) : (
                  <Button
                    className="block primary_bg"
                    variant="primary"
                    size="lg"
                    type="submit"
                  >
                    Proceed to Pay
                  </Button>
                )}
              </div>
            </Row>
          </Form>
        </div>
      </FormModal>
      <PaymentCheckout
        formData={formData}
        singleSlotData={singleSlotData}
        handleClose={handleCloseModal}
        show={paymentCheckoutModel}
      />
    </div>
  );
}
