import React, { useState, useEffect } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import AddNewAppointment from "./AddNewAppointment";
import IMAGES from "../../../assets/images/index";
import { useDispatch, useSelector } from "react-redux";
import { getAllSlots } from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import moment from "moment";
import { useLocation } from "react-router-dom";

const Slots = () => {
  const dispatch = useDispatch();
  const [singleSlotData, setSingleSlotData] = useState("");
  const [slotDate, setSlotDate] = useState(
    moment(new Date()).format("YYYY-MM-DD")
  );
  const location = useLocation();
  const appointmentId = location?.state?.appointmentId;
  const [isSlotSelected, setIsSlotSelected] = useState(false);
  const { allSlots } = useSelector((state) => state.doctorSchedule);

  useEffect(() => {
    if (slotDate) {
      const finalData = {
        date: slotDate,
        month: null,
      };
      dispatch(getAllSlots(finalData));
    }
  }, [dispatch, slotDate]);

  const handleSlotSelection = (data) => {
    setSingleSlotData(data);
    setIsSlotSelected(true);
  };

  const handleBlur = () => {
    setIsSlotSelected(true);
  };
  function getDayName(date = new Date(slotDate), locale = "en-US") {
    return date?.toLocaleDateString(locale, { weekday: "long" });
  }
  const dayName = getDayName();
  const [show, setShow] = useState(false);
  const handleShow = () => setShow(true);
  const handleClose = () => setShow(false);

  return (
    <>
      <h5>Available Slots</h5>
      <Card className="slotsSection mt-4">
        <Card.Body>
          <Row className="d-flex justify-content-between align-items-center mb-4">
            <Col xl={2} lg={4} md={4} sm={6}>
              <div className="">
                <Form.Control
                  type="date"
                  name="dob"
                  defaultValue={moment(new Date()).format("YYYY-MM-DD")}
                  min={moment(new Date()).format("YYYY-MM-DD")}
                  className="custom-date"
                  onChange={(e) => setSlotDate(e.target.value)}
                />
              </div>
            </Col>
            <Col md={4} lg={4} className="text-center">
              <h5 className="text-black fw-bold mb-0">
                {dayName} - {moment(slotDate).format("DD-MM-YYYY")}
              </h5>
            </Col>
            <Col md={4} sm={4}>
              <div className="d-flex justify-content-end radioGroup">
                <div className="d-flex align-items-center me-sm-4 me-2">
                  <span className="bookedSlot rounded-circle me-2">
                    <img className="" src={IMAGES.BOOKED_SLOT} alt="booked" />
                  </span>
                  <span>Booked Slots</span>
                </div>
                <div className="d-flex align-items-center">
                  <span className="availableSlot rounded-circle me-2">
                    <img
                      className=""
                      src={IMAGES.AVAILABLE_SLOT}
                      alt="booked"
                    />
                  </span>
                  <span>Available Slots</span>
                </div>
              </div>
            </Col>
          </Row>

          <div className="doctor_Details">
            <div className="slotContainer mt-4 pt-3" onBlur={handleBlur}>
              {allSlots !== null && allSlots?.length > 0 ? (
                allSlots?.map((slotItem) => {
                  return (
                    <Button
                      key={slotItem?.startTime}
                      className={`slot_btn${
                        slotItem?.isBooked ? " booked" : ""
                      }`}
                      onClick={() => handleSlotSelection(slotItem)}
                    >
                      {slotItem?.startTime} - {slotItem?.endTime}
                    </Button>
                  );
                })
              ) : (
                <p className="text-center">No Slot Available</p>
              )}
            </div>
          </div>
          <div className="float-right mt-4">
            <Button
              onClick={handleShow}
              className="btn-primary px-5 primary_bg"
              disabled={singleSlotData?.isBooked === true || !singleSlotData}
            >
              Continue
            </Button>
          </div>
        </Card.Body>
        <AddNewAppointment
          show={show}
          handleClose={handleClose}
          singleSlotData={singleSlotData}
          appointmentId={appointmentId}
        />
      </Card>
    </>
  );
};

export default Slots;
